body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Занимаем всю высоту экрана */
}



.wrapper {
  width: 70%;
  display: flex;
  background-color: #f7f7f7;
  border-radius: 20px;
  flex-direction: column;
  max-width: 600px;
  min-width: fit-content;
  align-items: center;

  font-weight: 400;
  padding: 20px;
}
.clock {
  color: var(--M3-sys-light-on-surface-variant, #49454f);
  text-align: center;
  letter-spacing: 0.5px;
  font: 16px/150% Roboto, sans-serif;
}
.currWeek {
  border-radius: 15px;
  background-color: #d7ddff;
  margin-top: 7px;
  color: #49454f;
  letter-spacing: 0.5px;
  justify-content: center;
  padding: 5px 26px;
  font: 16px/150% Roboto, sans-serif;
}
.dateNawWrap {
  border-radius: 16px;
  background-color: #ece6f0;
  display: flex;
  margin-top: 6px;
  font-size: 14px;
  white-space: nowrap;
  text-align: center;
  letter-spacing: 0.5px;
  justify-content: space-between;
  width: 70%;
  min-width: 290px;
  padding: 10px;
}
.dateElem {
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  padding: 7px 11px;
  transition: background-color 0.3s ease;
}

.dateSelected {
  background-color: rgba(0, 178, 255, 0.2);
}

.dateCurrent {
  background-color: rgba(79, 250, 198, 0.4);
}

.dateArrow {
  border-radius: 7px;
  padding: 7px 8px;
  display: flex;
  transition: transform 0.3s, background-color 0.3s;
}


.dateElem:hover, .dateArrow:hover {
  background-color: rgba(0, 178, 255, 0.2);
}

.dateSubElem1 {
  color: #1d1b20;
  font-family: Roboto, sans-serif;
}
.dateSubElem2 {
  color: var(--M3-sys-light-on-surface-variant, #49454f);
  font-family: Roboto, sans-serif;
}
.dateNav {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  flex-basis: 0;
  width: fit-content;
  margin: auto 0;
}
.dateLineGroup {
  display: flex;
  gap: 20px;
  color: #1d1b20;
  justify-content: space-between;
  padding: 0 1px;
}
.cardsListWrap {
  align-self: center;
  border-radius: 16px;
  background-color: #ece6f0;
  display: flex;
  margin-top: 8px;
  width: 95%;
  flex-direction: column;
  padding: 26px 14px;
  align-items: center;
}
.cardBody {
  border-radius: 15px;
  background-color: rgba(79, 250, 198, 0.4);
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 9px 1px 9px 11px;
  max-width: 400px;
  margin-top: 8px;
  width: 60%;
  min-width: 250px;
}

.mt-2 {
  margin-top: 20px;
}

.cardTimeSegment {
  align-self: stretch;
  display: flex;
  flex-direction: column;
  font-size: 14px;
  color: #1d1b20;
  white-space: nowrap;
  letter-spacing: 0.25px;
  line-height: 143%;
  margin: auto 0;
}
.cardUpTime {
  font-family: Roboto, sans-serif;
}
.cardDownTime {
  font-family: Roboto, sans-serif;
  margin-top: 12px;
}
.cardSlash {
  border-color: rgba(29, 27, 32, 1);
  background-color: #1d1b20;
  align-self: stretch;
  width: 2px;
  height: 45px;
  margin: auto 0;
}
.cardTextContainer {
  justify-content: center;
  align-self: stretch;
  display: flex;
  flex-direction: column;
}
.cardLessonText {
  color: var(--M3-sys-light-on-surface, #1d1b20);
  letter-spacing: 0.5px;
  font: 16px/150% Roboto, sans-serif;
}
.cardLessonType{
  color: var(--M3-sys-light-on-surface-variant, #49454f);
  text-overflow: ellipsis;
  letter-spacing: 0.25px;
  font: 14px/143% Roboto, sans-serif;
}
.partsSlash {
  border-radius: 15px;
  background-color: #D7DDFF;
  display: flex;
  margin-top: 8px;
  align-items: center;
  gap: 8px;
  width: 100%;
  padding: 1px 1px 9px 1px;
}